<template>
  <div class="hello">
    <h1>Generic Pay Connector - Agent Assist</h1>
    <h2 v-if="phoneNumber">Call {{ phoneNumber }}</h2>

    <button v-if="!deviceCreated" class="startup" @click="startDevice">Start up the device</button>
    <button v-if="deviceCreated" @click="stopDevice">Stop the device</button>

    <div v-if="deviceCreated">

      <div class="incoming-call" v-if="incomingCall">
        <p>Incoming Call</p>
        <button id="button-accept-incoming" @click="acceptCall">Accept</button>
        <button id="button-reject-incoming" @click="rejectCall">Reject</button>
      </div>

      <button v-if="activeCall" @click="hangupCall">Hangup</button>

      <section v-if="activeCall">
            <h2>Pay Session</h2>
            <form>
              <label for="payment-amount">Payment Amount
                <input id="payment-amount" :disabled="paymentSid !== null" v-model="paymentDetails.paymentAmount" />
              </label>
              <button @click.prevent="startPaymentSession" :disabled="paymentSid !== null">Start Payment Session</button>
            </form>
            <div v-if="paymentSid !== null">
              <h3>Payment Details</h3>
              <ul class="payment-details">
                <li>Card Number: <input :value="paymentDetails.cardNumber" disabled /></li>
                <li>Expiration Date: <input :value="paymentDetails.expirationDate" disabled /></li>
                <li>Postal Code: <input :value="paymentDetails.postalCode" disabled /></li>
                <li>Security Code: <input :value="paymentDetails.securityCode" disabled /></li>
              </ul>
              <h3 v-if="currentCaptureStep">Currently capturing: {{ currentCaptureStep }}</h3>
              <ul class="payment-prompts">
                <li><button @click="updatePaymentSession('payment-card-number')">Capture Payment Card Number</button></li>
                <li><button @click="updatePaymentSession('expiration-date')">Capture Expiration Date</button></li>
                <li><button @click="updatePaymentSession('postal-code')">Capture Postal Code</button></li>
                <li><button @click="updatePaymentSession('security-code')">Capture Security Code</button></li>
              </ul>
              <button @click.prevent="endPaymentSession">Complete Payment Session</button>
            </div> 
              <p v-if="paymentDetails.errorType">{{paymentDetails.errorType}}</p>
              <p v-if="successMessage">{{successMessage}}</p>
        
      </section>
      </div>
  </div>
</template>

<script>

import { Device } from '@twilio/voice-sdk';
import { SyncClient } from 'twilio-sync';
import { v4 as uuidv4 } from 'uuid';

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return {
      paymentDetails: {
        paymentAmount: "",
        cardNumber: '',
        expirationDate: '', 
        postalCode: '', 
        securityCode: '',
        errorType: '',
        result: '',
        paymentConfirmationCode: ''
      },
      paymentSessionStatus: "", 
      deviceCreated: false,
      activeCall: false, 
      incomingCall: false,
      syncClient: null, 
      paymentSid: null,
      successMessage: null,
      domain: null,
      phoneNumber: null,
      currentCaptureStep: null
    }
  }, 
  methods: {
    async startDevice() {     
      
      try {
        const resp = await fetch(`${this.domain}/token`);
        const token = await resp.json();

        this.$options.device = new Device(token.accessToken);
        this.phoneNumber = token.phoneNumber;
        this.deviceCreated = true;

        this.$options.device.on("registered", function () {
          console.log("Device ready to receive calls.")
        });

        this.$options.device.on("incoming", this.handleIncomingCall);
        this.$options.device.on("error", function(error) {
          console.log("Device error: " + error.message)
        });


        console.log('Registering to receive incoming calls.');
        this.$options.device.register();

        this.getSyncClient(token);

      } catch (e) {
        console.error('error during fetch', e.message);
      }

    },
    handleIncomingCall(call) {
      this.incomingCall = true;
      this.$options.activeCall = call;
    },
    stopDevice() {
      this.incomingCall = false;
      this.activeCall = false;
      this.$options.device.destroy();
      this.deviceCreated = false;
    },   
    log(message) {
      this.logMessages.push(message);
      this.$nextTick(() => {this.updateLogScrollHeight()});
    }, 
    updateLogScrollHeight() {
      const logDiv = this.$el.querySelector("#log");
      logDiv.scrollTop = logDiv.scrollHeight;
    }, 
    hangupCall() {
      this.$options.activeCall.disconnect();
      this.incomingCall = false;
      this.activeCall = false;
    }, 
    rejectCall() {
      this.incomingCall = false;
      this.$options.activeCall.reject();
      this.activeCall = false;
    },
    acceptCall() {
      this.incomingCall = false;
      this.activeCall = true;
      this.$options.activeCall.accept();

      this.$options.activeCall.on("cancel", this.handleDisconnectedCall);
      this.$options.activeCall.on("disconnect", this.handleDisconnectedCall);     

      this.createSyncDocument();
    }, 
    handleDisconnectedCall() {
      this.activeCall = false;
    }, 
    getParentCallSid() {
      return Array.from(this.$options.activeCall.customParameters.values())[0];
    },
    async startPaymentSession() {
      
      const url = `${this.domain}/startPaySession`

      const component = this;
      
      const parentCallSid = this.getParentCallSid();

      let response = await fetch(url , {
        method: 'POST', 
        mode: 'cors',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          paymentAmount: component.paymentDetails.paymentAmount,
          callSid: parentCallSid,
          syncDocumentSid: component.syncDocumentSid
        })
      })

      let data = await response.json();
      this.paymentSid = data.paymentSid
      console.log('Pay Session Started. SID: ', data.paymentSid)
    },
    async updatePaymentSession(paymentStep) {

      this.currentCaptureStep = paymentStep;

      const url = `${this.domain}/updatePaySession`

      const component = this;
      const parentCallSid = this.getParentCallSid();


      let response = await fetch(url , {
        method: 'POST', 
        mode: 'cors',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          paymentStep,
          paymentSid: component.paymentSid,
          callSid: parentCallSid, 
          syncDocumentSid: component.syncDocumentSid
        })
      })

      let data = await response.json();
      console.log('update response: ', data);      
    },
    async endPaymentSession() {

      const url = `${this.domain}/completePaySession`

      const component = this;
      const parentCallSid = this.getParentCallSid();


      await fetch(url , {
        method: 'POST', 
        mode: 'cors',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          paymentSid: component.paymentSid,
          callSid: parentCallSid,
          syncDocumentSid: component.syncDocumentSid
        })
      })

      this.currentCaptureStep = null;
    },
    getSyncClient(token) {
      this.syncClient = new SyncClient(token.accessToken, {logLevel: 'info'});

      this.syncClient.on('connectionStateChanged', function(state) {
       if (state === 'connected') {
         console.log('Sync Client Connected');
         
       }
      })
    },
    clearPaymentDetails() {
      this.paymentDetails= {
        paymentAmount: "",
        cardNumber: '',
        expirationDate: '', 
        postalCode: '', 
        securityCode: '',
        errorType: '',
        result: '',
        paymentConfirmationCode: ''
      }
    },
    createSyncDocument() {
      var component = this;

      this.syncDocumentSid = uuidv4();

      this.syncClient.document(this.syncDocumentSid).then(function(doc) {

        doc.on("updated", function(data) {
          console.log('Sync Document Updated', data);
          
          if (!data.isLocal) {
            console.log('Setting state with: ', data.data); 
            component.paymentDetails = data.data;

            if (component.paymentDetails.result === 'success') {
              component.paymentSid = null;
              component.successMessage = `Payment successful. Confirmation code: ${component.paymentDetails.paymentConfirmationCode}`;
              component.clearPaymentDetails();
            }
          }
        })
      })
    }
  }, 
  mounted() {
    this.domain = window.location.origin;
  },
}
</script>

